/**
 * The value that represents the theme to override the system theme with.
 *
 * - `'light'` - Light mode.
 *
 * - `'dark'` - Dark mode.
 *
 * - `'auto'` - Follow the default system theme.
 *
 * @default 'auto'
 */
export type InterfaceStyle = 'light' | 'dark' | 'auto';

/**
 * @hidden
 */
export type ChangeEventPayload = {
  theme: InterfaceStyle;
};
